.App {
  font-family: Manrope;
  font-style: normal;
  min-height: 100vh;
}

.App-inner {
  padding: 30px 0;
}

.App-title {
  margin-bottom: 35px;
  font-weight: 600;
  font-size: 40px;
  line-height: 55px;
}

.ContentCard {
  padding: 35px 55px;
}

.ContentCard-iconWrapper {
  margin-bottom: 50px;
  width: 90px;
  height: 90px;
  border-radius: 20px;
}
.ContentCard-iconWrapper--purple {
  background-color: #9672ff;
}
.ContentCard-iconWrapper--blue {
  background-color: #4ddffd;
}
.ContentCard-iconWrapper--pink {
  background-color: #f2b8ec;
}

.ContentCard-label {
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
}
.ContentCard-description {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 26px;
}

@media (max-width: 1200px) {
  .App-title {
    margin-bottom: 28px;
  }

  .ContentCard-iconWrapper {
    margin-bottom: 30px;
  }

  .ContentCard {
    padding: 28px;
  }
}

@media (max-width: 992px) {
  .App-title {
    margin-bottom: 22px;
  }

  .ContentCard-iconWrapper {
    margin-bottom: 20px;
  }

  .ContentCard {
    padding: 22px;
  }
}

@media (max-width: 576px) {
  .App-title {
    font-size: 28px;
  }
}
